___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #F6F8FD;
}

img {
  display: block;
}

.mt-5 {
  margin-top: 5rem;
}
.pt-5 {
  padding-top: 5rem;
}
.min-h-70 {
  min-height: 70vh;
}

.h-40 {
  height: 40rem;
}
.w-40 {
  width: 40rem;
}
.h-fluid {
  height: 90%;
  max-height: 30rem;
}
.w-fluid {
  width: 90%;
  margin: 0 auto;
  max-width: 30rem;
}
.w-full {
  width: 100%;
}
.text-center {
  text-align: center;
}
.restaurant-list {
  display: flex;
  flex-direction: column;
}

#restaurant-list__container {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.restaurant-list__title {
  font-size: 2rem;
  padding: 2rem;
  margin: 0 auto;
}

.skip-link {
  position: absolute;
  top: -50px;
  left: 50px;
  padding: 8px;
  background-color: #fff;
  color: #000;
  text-decoration: none;
  z-index: 100;
}

.skip-link:focus {
  top: 50px;
}

#searchForm {
  display: flex;
  width: 75%;
  max-width: 30rem;
  min-height: 45px;
  gap: 1rem;
  margin: 0 auto;
  item-align: center;
  margin-bottom: 20px;
}

#searchInput {
  flex: 1;
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

#searchInput:focus {
  outline: none;
  border-color: dodgerblue;
}

#searchForm button {
  padding: 8px 16px;
  color: #000;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #FFE500;
}

#searchForm button:hover {
  opacity: 0.8;
}

#searchForm button:focus {
  outline: none;
  box-shadow: 0 0 0 2px #ccc;
}


.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: 
    radial-gradient(farthest-side,#ffa516 94%,#0000) top/8px 8px no-repeat,
    conic-gradient(#0000 30%,#ffa516);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 8px),#000 0);
  animation: l13 1s infinite linear;
}
@keyframes l13{ 
  100%{transform: rotate(1turn)}
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;EACE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA;EACE,iCAAiC;EACjC,yBAAyB;AAC3B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,gBAAgB;AAClB;AACA;EACE,iBAAiB;AACnB;AACA;EACE,gBAAgB;AAClB;;AAEA;EACE,aAAa;AACf;AACA;EACE,YAAY;AACd;AACA;EACE,WAAW;EACX,iBAAiB;AACnB;AACA;EACE,UAAU;EACV,cAAc;EACd,gBAAgB;AAClB;AACA;EACE,WAAW;AACb;AACA;EACE,kBAAkB;AACpB;AACA;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,aAAa;EACb,cAAc;AAChB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,UAAU;EACV,YAAY;EACZ,sBAAsB;EACtB,WAAW;EACX,qBAAqB;EACrB,YAAY;AACd;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,aAAa;EACb,UAAU;EACV,gBAAgB;EAChB,gBAAgB;EAChB,SAAS;EACT,cAAc;EACd,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,YAAY;EACZ,eAAe;EACf,sBAAsB;EACtB,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,wBAAwB;AAC1B;;AAEA;EACE,iBAAiB;EACjB,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,0BAA0B;AAC5B;;;AAGA;EACE,WAAW;EACX,eAAe;EACf,kBAAkB;EAClB;;qCAEmC;EACnC,0EAA0E;EAC1E,iCAAiC;AACnC;AACA;EACE,KAAK,wBAAwB;AAC/B","sourcesContent":["* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\nbody {\n  font-family: 'Roboto', sans-serif;\n  background-color: #F6F8FD;\n}\n\nimg {\n  display: block;\n}\n\n.mt-5 {\n  margin-top: 5rem;\n}\n.pt-5 {\n  padding-top: 5rem;\n}\n.min-h-70 {\n  min-height: 70vh;\n}\n\n.h-40 {\n  height: 40rem;\n}\n.w-40 {\n  width: 40rem;\n}\n.h-fluid {\n  height: 90%;\n  max-height: 30rem;\n}\n.w-fluid {\n  width: 90%;\n  margin: 0 auto;\n  max-width: 30rem;\n}\n.w-full {\n  width: 100%;\n}\n.text-center {\n  text-align: center;\n}\n.restaurant-list {\n  display: flex;\n  flex-direction: column;\n}\n\n#restaurant-list__container {\n  display: flex;\n  justify-content: center;\n  margin: 0 auto;\n}\n\n.restaurant-list__title {\n  font-size: 2rem;\n  padding: 2rem;\n  margin: 0 auto;\n}\n\n.skip-link {\n  position: absolute;\n  top: -50px;\n  left: 50px;\n 